export default {
	methods: {
		/**
		 * 
		 * @param {Object} out_trade_no
		 */
		orderPay(orderData) {
			if (orderData.adjust_money == 0) {
				this.openChoosePayment();
			} else {
				uni.showModal({
					title: '',
					content: '' + orderData.pay_money + '',
					success: res => {
						if (res.confirm) {
							this.openChoosePayment();
						}
					}
				})
			}
		},
		pay() {
			this.$api.sendRequest({
				url: '/api/order/pay',
				data: {
					order_ids: this.orderData.order_id
				},
				success: res => {
					if (res.code >= 0) {
						this.$refs.choosePaymentPopup.getPayInfo(res.data);
					} else {
						this.$util.showToast({
							title: res.message
						});
					}
				}
			})
		},
		/**
		 * 
		 * @param {Object} order_id
		 */
		orderClose(order_id, callback) {
			uni.showModal({
				title: '',
				content: '',
				success: res => {
					if (res.confirm) {
						this.$api.sendRequest({
							url: '/api/order/close',
							data: {
								order_id
							},
							success: res => {
								if (res.code >= 0) {
									typeof callback == 'function' && callback();
								} else {
									this.$util.showToast({
										title: '' + res.message + '!',
										duration: 2000
									})
								}
							}
						})
					}
				}
			})
		},
		/**
		 * 
		 * @param {Object} order_id
		 */
		orderDelivery(order_id, callback) {
			uni.showModal({
				title: '',
				content: '',
				success: res => {
					if (res.confirm) {
						this.$api.sendRequest({
							url: '/api/order/takedelivery',
							data: {
								order_id
							},
							success: res => {
								this.$util.showToast({
									title: res.message
								})
								typeof callback == 'function' && callback();
							}
						})
					}
				},
			})
		},
		/**
		 * 
		 * @param {Object} order_id
		 */
		orderExtendTakeDelivery(order_id, callback) {
			uni.showModal({
				title: '',
				content: '\n ',
				// showCancel: false,
				success: res => {
					if (res.confirm) {
						this.$api.sendRequest({
							url: '/api/order/extendtakedelivery',
							data: {
								order_id
							},
							success: res => {
								this.$util.showToast({
									title: res.message
								})
								typeof callback == 'function' && callback();
							}
						})
					}
				},
			})
		},
	}
}
