/**
 * jssdk
 */
let Weixin = function() {
	var wx = require('jweixin-module');

	this.init = function(params) {
		wx.config({
			debug: false, // ,apialertpclogpc
			appId: params.appId, // 
			timestamp: params.timestamp, // 
			nonceStr: params.nonceStr, // 
			signature: params.signature, // 
			jsApiList: ['chooseWXPay', 'openAddress', 'updateAppMessageShareData', 'updateTimelineShareData', 'scanQRCode'] // JS
		});
	}

	/**
	 * 
	 * @param {Object} jsApiParame
	 * @param {Object} callback
	 */
	this.pay = function(jsApiParame, callback) {
		wx.ready(function() {
			wx.chooseWXPay({
				timestamp: jsApiParame.timestamp, // jssdktimestamptimeStampS
				nonceStr: jsApiParame.nonceStr, //  32 
				package: jsApiParame.package, // prepay_idprepay_id=\*\*\*
				signType: jsApiParame.signType, // 'SHA1''MD5'
				paySign: jsApiParame.paySign, // 
				success: function(res) {
					typeof callback == 'function' && callback(res);
				}
			});
		})
	}

	/**
	 * 
	 * @param {Object} callback
	 */
	this.openAddress = function(callback) {
		wx.ready(function() {
			wx.openAddress({
				success: function(res) {
					typeof callback == 'function' && callback(res);
				},
				fail: (res) => {
					alert(JSON.stringify(res))
				}
			});
		})
	}


	/**
	 * 
	 * @param {Object} params
	 * @param {Object} callback
	 */
	this.setShareData = function(params, callback) {
		wx.ready(function() {
			// QQ
			wx.updateAppMessageShareData({
				title: params.title || '', // 
				desc: params.desc || '', // 
				link: params.link || '', // JS
				imgUrl: params.imgUrl || '', // 
				success: function() {
					typeof callback == 'function' && callback(res);
				}
			})
			// QQ
			wx.updateTimelineShareData({
				title: params.title || '', // 
				link: params.link || '', // JS
				imgUrl: params.imgUrl || '', // 
				success: function() {
					typeof callback == 'function' && callback(res);
				}
			})
		});
	}

	/**
	 * 
	 * @param {Object} callback
	 */
	this.scanQRCode = function(callback) {
		wx.ready(function() {
			wx.scanQRCode({
				needResult: 1,
				scanType: ["qrCode"],
				success: function(res) {
					typeof callback == 'function' && callback(res);
				}
			});
		})
	}
}

export {
	Weixin
}
